.closeHadith {
  background: none;
  border: none;
  border-radius: 100%;
  cursor: pointer;
  height: 25px;
  outline: inherit;
  position: fixed;
  right: 5%;
  top: 15%;
  width: 25px;
  font-size: 20px;
  color: #605b56;
}

.hadith-container {
  position: fixed;
  height: 70%;
  width: 80%;
  text-align: center;
  margin: auto;
  right: 0;
  left: 0;
  top: 20%;
  border-radius: 11px;
  padding: 6px 0px 0px 0px;
}

.hadith-container .hadith-container-current {
  background-color: #605b56;
  padding: 8.1px;
  border-radius: 8px;
  height: 85%;
  overflow: auto;
  text-align: left;
}

.hadith-container .hadith-container-current .hadith-container-current-title {
  color: #acc18a;
}

.TodayTimesList__ul-times-list {
  width: 90%;
  color: white;
  margin: 5% auto auto auto;
  padding: 0;
}

.TodayTimesList__ul-times-list .TodayTimesList__ul-times-list__li {
  -webkit-box-shadow: -2px 5px 6px 1px rgba(0, 0, 0, 0.26);
  box-shadow: -2px 5px 6px 1px rgba(0, 0, 0, 0.26);
  font-weight: bold;
  color: #605b56;
  width: 100%;
  background-color: #acc18a;
  list-style: none;
  height: 50px;
  border-radius: 8px;
  text-align: center;
  margin-bottom: 8px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  font-size: 13px;
  -webkit-transition: 2s;
  transition: 2s;
}

.TodayTimesList__ul-times-list .TodayTimesList__ul-times-list__li .TodayTimesList__ul-times-list__li__span--information {
  padding-left: 84%;
  position: absolute;
}

.TodayTimesList__ul-times-list .TodayTimesList__ul-times-list__li .TodayTimesList__ul-times-list__li__span--information .info-icons {
  cursor: pointer;
}

@media (min-width: 622px) {
  .TodayTimesList__ul-times-list {
    width: 90%;
    color: white;
    margin: 5% 5% 5% 5%;
    padding: 0;
  }
  .TodayTimesList__ul-times-list .TodayTimesList__ul-times-list__li {
    font-weight: bold;
    color: #605b56;
    width: 100%;
    background-color: #acc18a;
    list-style: none;
    height: 50px;
    border-radius: 8px;
    text-align: center;
    margin-bottom: 8px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    font-size: 13px;
  }
  .TodayTimesList__ul-times-list .TodayTimesList__ul-times-list__li .TodayTimesList__ul-times-list__li__span--information {
    margin-left: -48%;
    position: absolute;
  }
  .TodayTimesList__ul-times-list .TodayTimesList__ul-times-list__li .TodayTimesList__ul-times-list__li__span--information .info-icons {
    cursor: pointer;
  }
}
